import { useAuth } from "context/authContext";
import { AuthInput } from "components/AuthInput";
import { ReactComponent as LogoSVG } from "assets/Icon.svg";
import styles from "pages/LoginRegister.module.scss";
import { Link } from "react-router-dom";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import clsx from "clsx";
import { Verified } from "./mainpage/verify";

export const LoginPage = () => {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const { login } = useAuth();
  const id = localStorage.getItem("id");
  const [accountPassed, setAccountPassed] = useState(true);
  const [pwdPassed, setPwdPassed] = useState(true);

  Verified();

  const handleLogin = async () => {
    if (account.length === 0) return;
    if (password.length === 0) return;

    const res = await login({ account, password });
    if (res.success) {
      setAccountPassed(true);
      Swal.fire({
        title: "!",
        icon: "success",
        iconColor: "#82C43C",
        showConfirmButton: false,
        timer: 1000,
        position: "top",
      });
      setInterval(() => {
        if (!id) {
          console.log(id);
          console.log("reload");
          window.location.reload();
        } else {
          console.log(id);
          navigate("/home");
          return;
        }
      }, 1000);
    } else if (res.response.data === "Account incorrect") {
      console.log(res.response.data);
      setAccountPassed(false);
      Swal.fire({
        title: "!",
        text: "!",
        icon: "error",
        showConfirmButton: false,
        timer: 1000,
        position: "top",
      });
      return;
    } else if (res.response.data === "Password incorrect") {
      setPwdPassed(false);
      Swal.fire({
        title: "!",
        text: "!",
        icon: "error",
        showConfirmButton: false,
        timer: 1000,
        position: "top",
      });
      return;
    } else if (
      res.response.data ===
      "I'm sorry, but access to the frontstage area is restricted."
    ) {
      setAccountPassed(false);
      Swal.fire({
        title: "!",
        text: "!",
        icon: "error",
        showConfirmButton: false,
        timer: 1000,
        position: "top",
      });
      return;
    }
  };

  return (
    <div className={styles.authContainer}>
      <div>
        <LogoSVG />
      </div>
      <h1> Alphitter</h1>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={account}
          accountPassed={accountPassed}
          label=""
          placeholder=""
          onChange={(e) => {
            setAccount(e.target.value);
            setAccountPassed(true);
          }}
          onKeyDown={handleLogin}
        />
        <span
          className={clsx("", {
            [styles.limit]: !accountPassed,
            [styles.noLimit]: accountPassed,
          })}
        >
          !
        </span>
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="password"
          label=""
          value={password}
          placeholder=""
          onChange={(e) => {
            setPassword(e.target.value);
            setPwdPassed(true);
          }}
          pwdPassed={pwdPassed}
          onKeyDown={handleLogin}
        />
        <span
          className={clsx("", {
            [styles.limit]: !pwdPassed,
            [styles.noLimit]: pwdPassed,
          })}
        >
          !
        </span>
      </div>
      <button className={styles.loginButton} onClick={handleLogin}>
        
      </button>
      <div className={styles.linkText}>
        <Link to="/register"></Link> {" "}
        <Link to="/admin/login"></Link>
      </div>
    </div>
  );
};
